import _ from "lodash";
import { useSelector } from "react-redux";
import "./Pay.scss";

export default function Pay() {
  const cartList = useSelector((state) => state.cart.cartList);
  const totalPriceCart = useSelector((state) => state.cart.totalAmount);
  console.log(cartList);

  return (
    <div className="pay-container">
      <div className="pay-wrap">
        <div className="pay-message">
          <span>Bn c m u i? </span>
          <a className="toggle-coupon">n vo y  nhp m</a>
        </div>

        <form className="pay-coupon">
          <p>Nu bn c m gim gi, vui lng in vo pha bn di.</p>
          <div className="enter-coupon">
            <input placeholder="M u i" />
            <button className="submit">P DNG</button>
          </div>
        </form>

        <div className="pay-main">
          <div className="pay-left">
            <div className="billing-info">
              <h3>THNG TIN THANH TON</h3>

              <p className="info-fullname info-item">
                <label htmlFor="fullName">
                  H v tn <span>*</span>
                </label>
                <input type="text" id="fullName" />
              </p>

              <p className="info-address info-item">
                <label htmlFor="address">
                  a ch <span>*</span>
                </label>
                <input type="text" id="address" />
              </p>

              <p className="info-phonenumber info-item">
                <label htmlFor="phoneNumber">
                  S in thoi <span>*</span>
                </label>
                <input id="phoneNumber" type="number" />
              </p>

              <p className="info-email info-item">
                <label htmlFor="email">
                  a ch email <span>*</span>
                </label>
                <input id="email" type="email" />
              </p>
            </div>

            <div className="add-info">
              <h3>THNG TI B SUNG</h3>
              <p className="info-message">
                <label htmlFor="message">Ghi ch n hng (ty chn)</label>
                <textarea
                  name="message"
                  id="message"
                  placeholder="Ghi ch v n hng"
                ></textarea>
              </p>
            </div>
          </div>

          <div className="pay-right">
            <div className="order-review">
              <h3>N HNG CA BN</h3>
              <table className="order-table">
                <thead>
                  <tr className="order-header">
                    <th className="order-name">SN PHM</th>
                    <th className="product-subtotal text-right">TM TNH</th>
                  </tr>
                </thead>

                {cartList.length > 0 &&
                  _.map(cartList, (product, index) => {
                    return (
                      <tbody key={index}>
                        <tr className="order-product">
                          <td>
                            {product.name} x {product.quantity}
                          </td>
                          <td className="text-right">
                            {product.price * product.quantity}.000 
                          </td>
                        </tr>
                      </tbody>
                    );
                  })}

                <tfoot>
                  <tr className="order-subtotal">
                    <th>Tm tnh</th>
                    <td className="text-right">{totalPriceCart}.000 </td>
                  </tr>
                  <tr className="order-total">
                    <th>Tng</th>
                    <td className="text-right">{totalPriceCart}.000 </td>
                  </tr>
                </tfoot>
              </table>

              <div className="checkout-payment">
                <p>Tr tin mt khi nhn hng</p>
                <p>Tr tin mt khi giao hng</p>
                <button>T HNG</button>
                <p className="policy-link">
                  <a>Xem chnh sch ring t</a>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
