import "./SewingSet.scss";
import { useDispatch, useSelector } from "react-redux";
import { toggleRequest } from "../../../../redux/Slices/navbarSlice";
import { UseOutsideClick } from "../../../../hooks/useOutsideClick";

export default function SewingSet() {
  const disPatch = useDispatch();
  const visSewing = useSelector((state) => state.navbar.order);

  const ref = UseOutsideClick(() => disPatch(toggleRequest(false)));
  return (
    <div className={`sewing-container ${visSewing ? "open" : "close"}`}>
      <button
        className="close-sewing"
        onClick={() => disPatch(toggleRequest(false))}
      >
        x
      </button>
      <div ref={ref} className="sewing-wrap">
        <h3>T MAY THEO YU CU</h3>
        <form className="sewing-form">
          <p className="form-item">
            <label htmlFor="fullname">H v tn</label>
            <input type="text" name="" />
          </p>
          <p className="form-item">
            <label htmlFor="your-phone">S in thoi</label>
            <input type="number" name="your-phone" />
          </p>
          <p className="form-item">
            <label htmlFor="height">Chiu cao</label>
            <input type="text" name="height" />
          </p>
          <p className="form-item">
            <label htmlFor="weight">Cn nng</label>
            <input type="text" name="weight" />
          </p>
          <p className="form-sample">
            <label htmlFor="sample">Mu cn t</label>
            <input type="file" name="sample" />
          </p>
          <p className="form-message">
            <label htmlFor="message">Li nhn</label>
            <textarea name="your-message"></textarea>
          </p>
          <p className="form-submit">
            <button type="submit">GI I</button>
          </p>
        </form>
      </div>
    </div>
  );
}
